.product{
    width:300px;
    padding: 1rem;
    background: steelblue;
    -webkit-box-shadow: 4px 5px 7px 1px rgba(0,0,0,0.8); 
    box-shadow: 4px 5px 7px 1px rgba(0,0,0,0.8);
    margin:8px auto;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}

.product > img {
    height: 170px;
    border-radius: 20px;
}

.product__info > p{
    margin-bottom: 8px;
}

.info__price {
    font-weight: bold;
    margin: 1rem 0;
}

.info__name{
    margin: 1rem 0;
    font-size: 1rem;
    overflow: hidden;
    font-weight: bold;
}

.info__description{
    font-size: 0.8rem;
}

.info__button{
    display: block;
    width: 100%;
    text-decoration: none;
    text-align: center;
    color: #171717;
    background: slategray;
    padding: 8px 16px;
    border: 1px  solid #171717;
    font-size: 1rem;
}
.info__button:hover{
    background: lightskyblue;
    font-size: 1.2rem;
    border-radius: 10px;
}

@media (max-width:1232px) {
    .product {
        width: 360px;
    }
}

@media (max-width:1115px) {
    .product {
        width: 330px;
    }
}

@media (max-width:1028px) {
    .product {
        width: 300px;
    }
}

@media (max-width:950px) {
